.image-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 0 10px;
    grid-auto-rows: 10px;
}

.image-list img {
    width: 250px;
}

button {
    position: relative;
    bottom: 10px;
    left: 40%;
    right: 40%;
}
.image-display button {
    position: relative;
    top: 5px;
    right: 10px;
    font-size: 46px;
    background-color: inherit;
    border: none;
    color: white;
    z-index: 1;
}

.image-display img {
    max-height: 600px;
    max-width: auto;
}

.image-display {
    margin-top: 5px;
}
body {
    background-color: rgb(65, 61, 61);
    color: white;
}

.ui .segment {
    background-color: rgb(105, 92, 92);
}

.image-list img:hover {
    position:relative;
    top:-5px;
    left:-35px;
    width:auto;
    height:150%;
    display:block;
    z-index:1;
    border-radius: 2px;
}
